import React from "react";
import {
  Document,
  Page,
  Text,
  View,
  StyleSheet,
  PDFDownloadLink,
} from "@react-pdf/renderer";
import { Sale } from "@/types/type";

// Styles for the PDF
const styles = StyleSheet.create({
  page: {
    backgroundColor: "#f3f4f6",
    padding: 20,
    fontSize: 10,
    fontFamily: "Helvetica",
  },
  card: {
    backgroundColor: "#ffffff",
    borderRadius: 10,
    padding: 15,
    marginBottom: 10,
    shadowColor: "#000000",
    shadowOpacity: 0.1,
    shadowRadius: 4,
    shadowOffset: { width: 0, height: 2 },
  },
  header: {
    fontSize: 16,
    fontWeight: "bold",
    textAlign: "center",
    color: "#16a34a",
    marginBottom: 5,
  },
  subHeader: {
    fontSize: 8,
    textAlign: "center",
    color: "#4b5563",
  },
  row: {
    flexDirection: "row",
    justifyContent: "space-between",
    marginVertical: 2,
  },
  label: { color: "#374151", width: "40%", fontWeight: "bold" },
  value: { color: "#111827", width: "55%", textAlign: "right" },
  tableHeader: {
    flexDirection: "row",
    borderBottomWidth: 1,
    borderBottomColor: "#d1d5db",
    paddingBottom: 2,
    marginBottom: 2,
  },
  tableCell: { width: "20%", textAlign: "center" },
  tableCellRight: { width: "20%", textAlign: "right" },
});

// Function to calculate warranty months
const monthsDiff = (start: Date, end: Date) =>
  (end.getFullYear() - start.getFullYear()) * 12 +
  (end.getMonth() - start.getMonth());

interface Props {
  data: Sale;
}

const VoucherPDF = ({ data }: Props) => {
  const warrantyMonths = monthsDiff(
    new Date(data.saleDate),
    new Date(data.warrantyExpiry)
  );

  const formatCurrency = (amount: number) =>
    new Intl.NumberFormat("en-US", {
      style: "currency",
      currency: "MMK",
      minimumFractionDigits: 0,
    }).format(amount);

  const formatDate = (dateString: string) =>
    new Date(dateString).toLocaleDateString("en-US", {
      year: "numeric",
      month: "long",
      day: "numeric",
    });

  return (
    <Document>
      <Page size="A4" style={styles.page}>
        {/* Header */}
        <View style={styles.card}>
          <Text style={styles.header}>KYAW GYI BATTERY</Text>
          <Text style={styles.subHeader}>MingalarDon Township, Yangon</Text>
          <Text style={styles.subHeader}>ShwePyiThar Township, Yangon</Text>
          <Text style={styles.subHeader}>â˜Ž 09-123456789 , 09-123456789</Text>
        </View>

        {/* Invoice Info */}
        <View style={styles.card}>
          <View style={styles.row}>
            <Text style={styles.label}>Invoice:</Text>
            <Text style={styles.value}>{data.invoiceNumber}</Text>
          </View>
          <View style={styles.row}>
            <Text style={styles.label}>Date:</Text>
            <Text style={styles.value}>
              {formatDate(data.saleDate.toString())}
            </Text>
          </View>
          <View style={styles.row}>
            <Text style={styles.label}>Car No:</Text>
            <Text style={styles.value}>{data.carNumber}</Text>
          </View>
        </View>

        {/* Sale Products */}
        <View style={styles.card}>
          <Text style={{ fontWeight: "bold", marginBottom: 4 }}>
            Sale Products
          </Text>
          <View style={styles.tableHeader}>
            <Text style={{ width: "40%" }}>Product</Text>
            <Text style={styles.tableCell}>Qty</Text>
            <Text style={styles.tableCell}>Warranty</Text>
            <Text style={styles.tableCellRight}>Price</Text>
            <Text style={styles.tableCellRight}>Total</Text>
          </View>
          <View style={styles.row}>
            <Text style={{ width: "40%" }}>{data.product.name}</Text>
            <Text style={styles.tableCell}>{data.quantity}</Text>
            <Text style={styles.tableCell}>
              {warrantyMonths} {warrantyMonths > 1 ? "months" : "month"}
            </Text>
            <Text style={styles.tableCellRight}>
              {formatCurrency(data.salePrice)}
            </Text>
            <Text style={styles.tableCellRight}>
              {formatCurrency(data.salePrice * data.quantity)}
            </Text>
          </View>
        </View>

        {/* Payment */}
        <View style={styles.card}>
          <Text style={{ fontWeight: "bold", marginBottom: 4 }}>Payment</Text>
          <View style={styles.row}>
            <Text style={styles.label}>Method:</Text>
            <Text style={styles.value}>{data.paymentMethod}</Text>
          </View>
          <View style={styles.row}>
            <Text style={styles.label}>Status:</Text>
            <Text
              style={{
                ...styles.value,
                color: data.isPaid ? "#16a34a" : "#dc2626",
              }}
            >
              {data.isPaid
                ? "PAID"
                : data.duePayment > 0
                ? "UNPAID"
                : "PARTIAL"}
            </Text>
          </View>
          <View style={styles.row}>
            <Text style={styles.label}>Paid:</Text>
            <Text style={styles.value}>
              {data.rebuyOldBattery
                ? `${formatCurrency(data.totalPrice)} - ${formatCurrency(
                    data.oldBatteryPrice
                  )} = ${formatCurrency(data.paidAmount)}`
                : formatCurrency(data.paidAmount)}
            </Text>
          </View>
          {!data.isPaid && (
            <View style={styles.row}>
              <Text style={styles.label}>Due:</Text>
              <Text style={{ ...styles.value, color: "#dc2626" }}>
                {formatCurrency(data.duePayment)}
              </Text>
            </View>
          )}
        </View>

        {/* Footer */}
        <View style={{ marginTop: 10, textAlign: "center" }}>
          <Text>Thank you for your purchase!</Text>
          <Text>All batteries come with warranty as per shop policy.</Text>
          <Text>Please keep this voucher for future reference.</Text>
        </View>
      </Page>
    </Document>
  );
};

// Wrapper to download
export const VoucherDownload = ({ data }: Props) => (
  <PDFDownloadLink
    document={<VoucherPDF data={data} />}
    fileName={`voucher_${data.carNumber}.pdf`}
  >
    {({ loading }) =>
      loading ? (
        <button className="p-2 bg-gray-300 rounded">Loading...</button>
      ) : (
        <button className="p-2 bg-blue-600 text-white rounded hover:bg-blue-700 flex items-center gap-2">
          Download PDF
        </button>
      )
    }
  </PDFDownloadLink>
);
